const URI = require("urijs");

export default () => {
  let query = URI(location.href).query(true);
  if (query) {
    if (query.type) {  // ËØ∑Ê±ÇÂèÇÊï∞ÊúÄÈ´ò‰ºòÂÖàÁ∫ß
      return query.type;
    } else if (query.id) { // Ê†πÊçÆIDÊ†ºÂºèÂà§Êñ≠Á±ªÂûã
      return query.id.split('_').length > 1 ? 'item' : 'achievement';
    }
  }
  return 'achievement';   // ÈªòËÆ§Á±ªÂûã
};
