                />
                <span class="u-text">{{ source.Name }}</span>
            </span>
            <span class="u-star">
                <span>ÈöæÂ∫¶</span>
                &nbsp;
                <span
                    id="stars"
                    v-text="$options.filters.star(post.level)"
                ></span>
            </span>
            <span class="u-group">
                <em>„ÄêÊÑèËßÅÂèçÈ¶àQQÁæ§„Äë</em>
                <b>614370825</b>
            </span>
        </div>
        <div class="m-block m-content">
            <!-- Warning -->
            <div class="m-warning" :class="{ hide: !warning }">
                ‚ùå ÊÇ®ÁöÑÊµèËßàÂô®ÁâàÊú¨Â§™‰Ωé,Â∞ÜÊó†Ê≥ïÊ≠£Â∏∏‰ΩøÁî®Êú¨Â∫îÁî®
            </div>

            <!-- Panel -->
            <div class="m-title" id="title">
                <span class="u-label">üìã {{ type | moduleName }}ÊîªÁï•</span>
                <a
                    class="u-zhtr"
                    :class="{ on: isTW }"
                    @click="translateHandler"
                    @click.once="translateTrigger"
                    id="translator"
                >
                    üå∏[
                    <span class="u-tr">ÁπÅÈ´î</span>
                    <span class="u-cn">ÁÆÄ‰Ωì</span>
                    ]
                </a>
                <span class="u-date" v-if="post.updated">
                    <span>ÊúÄÂêéÊõ¥Êñ∞‰∫é</span>
                    <em
                        id="updated"
                        v-text="$options.filters.dateFormat(post.updated)"
                    ></em>
                </span>
                <a
                    class="u-edit"
                    :class="{ on: isEditMode }"
                    id="edit"
                    @click="editHandler($event)"
                    >üìùÁºñËæë‰øÆËÆ¢</a
                >
            </div>
            <!-- Article -->
            <div class="m-section u-content" style="padding:0">
                <div
                    v-show="content.trim() || isEditMode"
                    :contenteditable="isEditMode ? true : false"
                    @input.once="changeHandler"
                    id="content"
                    :class="{
                        isEditable: isEditMode,
                        hide: isTW,
                    }"
                >
                    <Article :content="content" :pageable="false" />
                </div>
                <div v-show="!content.trim() && !isEditMode" class="u-null">
                    üíß ÊöÇÊó†ÊîªÁï• ,
                    <a class="u-edit" @click="editHandler($event)">ÊàëÊù•ÂÜô</a>
                </div>
            </div>
            <div
                class="m-section u-content"
                :class="{ hide: !isTW }"
                id="content-tw"
                v-html="content_tw"
            ></div>
            <!-- Tips -->
            <div class="m-tips" :class="{ hide: !isEditMode }">
                Ê∏∏ÊàèÂÜÖ‰ªÖÊîØÊåÅÁÆÄÊòìÊñáÊú¨‰øÆËÆ¢,Â¶ÇÈúÄ‰∏ä‰º†ÂõæÁâáÁ≠â,ËØ∑Ëá≥<a
                    href="https://www.jx3box.com"
                    >JX3BOXÁΩëÁ´ô</a
                >Êìç‰Ωú
            </div>
            <!-- Author -->
            <div class="m-author isEditable" :class="{ hide: !isEditMode }">
                <div class="m-other-fields">
                    <div class="u-level">
                        <span>ÈöæÂ∫¶(1-5)Ôºö </span>
                        <input
                            type="number"
                            id="level"
                            min="1"
                            max="5"
                            v-model="publish.level"
                        />
                    </div>
                    <div class="u-author">
                        <span>‰ΩúËÄÖÔºö </span>
                        <input
                            type="text"
                            id="author"
                            v-model="publish.author"
                            autocomplete="off"
                        />
                    </div>
                    <div class="u-remark">
                        <span>‰øÆËÆ¢ËØ¥ÊòéÔºö </span>
                        <input
                            type="text"
                            id="remark"
                            v-model="publish.remark"
                            autocomplete="off"
                        />
                    </div>
                </div>
                <div class="u-btn">
                    <a
                        class="u-btn-cancel"
                        id="cancel"
                        @click="cancelHandler($event)"
                        >ÂèñÊ∂à</a
                    >
                    <a
                        class="u-btn-submit"
                        id="submit"
                        @click="submitHanlder($event)"
                        >Êèê‰∫§</a
                    >
                </div>
            </div>
        </div>
    </div>
</template>

<script>
const { JX3BOX, Utils } = require("@jx3box/jx3box-common");
const axios = require("axios");
const _ = require("lodash");
let qs = require("qs");
import { ua as UA } from "../utils";
import cn2tw from "../utils/cn2tw";
import Article from "@jx3box/jx3box-editor/src/Article.vue";
import { get_wiki_newest_post, get_wiki_post } from "../service/wiki_post";
import { getStat, postStat } from "@jx3box/jx3box-common/js/stat";

export default {
    name: "Content",
    props: ["query"],
    data() {
        return {
            type: this.$options.filters.source_type(),
            source: {},
            post_id: null,
            post: {},
            isEditMode: false,
            isChanged: false,
            isTW: false,
            publish: {
                level: 1,
                author: this.$options.filters.playerName(this.query.player),
                remark: "",
            },
            ua: {},
            content_tw: "",
        };
    },
    computed: {
        content: function() {
            let content =
                this.post &&
                _.get(this, "post.content", "") &&
                Utils.resolveImagePath(_.get(this, "post.content", ""));
            content = content.replace(
                /(<p>)?\s*‚óÜÊàêÂ∞±ÈöæÂ∫¶ [‚òÖ]+\s*(<\/p>)?/gi,
                ""
            );
            content = content.replace(
                /(<p>)?\s*‚óÜËä±Ë¥πÊó∂Èïø [‚òÖ]+\s*(<\/p>)?/gi,
                ""
            );
            content = content.replace(/(<p>)?\s*‚óÜÊàêÂ∞±ÊîªÁï•\s*(<\/p>)?/gi, "");
            return content ? content : " ";
        },
        warning: function() {
            return this.ua.browser == "ie" && this.ua.version < 9
                ? true
                : false;
        },
    },
    methods: {
        setPostId(post_id) {
            this.post_id = post_id;
            this.$emit("setPostId", post_id);
        },
        editHandler: function(e) {
            e.preventDefault();
            this.isTW = false;
            this.isEditMode = true;
        },
        cancelHandler: function(e) {
            e.preventDefault();
            this.isEditMode = false;
        },
        submitHanlder: function(e) {
            e.preventDefault();

            if (!this.isChanged) {
                alert("Ê≤°Êúâ‰ªª‰ΩïÊîπÂä®ÔºåËØ∑ÂãøÊª•Êèê‰∫§");
                return;
            }

            if (!this.publish.remark) {
                alert("ËØ∑ÁÆÄÂçïÊèèËø∞Êú¨Ê¨°‰øÆËÆ¢ËØ¥Êòé");
                return;
            }

            // LevelÊ†°È™å
            let level = this.publish.level
                ? this.publish.level
                : this.post.level;

            // Áî®Êà∑Âêç
            let author = this.$options.filters.playerName(this.publish.author);

            axios({
                method: "POST",
                url: `${JX3BOX.__helperUrl}api/wiki/post`,
                headers: { Accept: "application/prs.helper.v2+json" },
                data: qs.stringify({
                    post: {
                        type: this.type,
                        source_id: this.query.id,
                        level: level,
                        user_nickname: author,
                        content: document.getElementById("c-article").innerHTML,
                        remark: this.publish.remark,
                    },
                }),
            })
                .then((data) => {
                    data = data.data;
                    if (data.code === 200) {
                        alert("‚úîÔ∏è Êèê‰∫§ÊàêÂäü,ËØ∑Á≠âÂæÖÂÆ°Ê†∏");
                    } else {
                        alert(`‚ö†Ô∏è ${data.message}`);
                    }
                })
                .catch((err) => {
                    alert("‚ö†Ô∏è ÁΩëÁªúÂºÇÂ∏∏,Êèê‰∫§Â§±Ë¥•");
                })
                .finally(() => {
                    this.isEditMode = false;
                });
        },
        changeHandler: function(e) {
            this.isChanged = true;
        },
        translateHandler: function(e) {
            e.preventDefault();
            this.isTW = !this.isTW;
        },
        translateTrigger: function() {
            this.content_tw = cn2tw(this.content);
        },
        // Ëé∑ÂèñÊúÄÊñ∞ÊîªÁï•
        getWikiNewestPost(supply = 1) {
            if (this.query.id) {
                let source_id = this.query.id;
                let $http = null;
                switch (this.type) {
                    case "achievement":
                    case "item":
                        $http = get_wiki_newest_post(
                            this.type,
                            source_id,
                            supply
                        );
                        break;
                }
                $http &&
                    $http
                        .then((res) => {
                            this.post = res.data.data.post || {};
                            this.publish.level = _.get(this.post, "level");
                            this.source = res.data.data.source;
                            this.setPostId(this.post.id);
                        })
                        .catch((err) => {
                            this.post.content = "‚ö†Ô∏è Êï∞ÊçÆÂä†ËΩΩÂºÇÂ∏∏";
                        })
            }

            // ÁªüËÆ°
            let stat_type = this.type =='achievement' ? 'cj' : 'item'
            postStat(stat_type,this.query.id);
            
        },
    },
    mounted: function() {
        this.ua = UA();

        // Ëé∑ÂèñÊúÄÊñ∞ÊîªÁï•
        this.getWikiNewestPost();
    },
    watch: {
        post_id() {
            // Ëé∑ÂèñÊîªÁï•
            if (this.post_id) {
                get_wiki_post(this.post_id)
                    .then((res) => {
                        this.post = res.data.data.post || {};
                        this.publish.level = _.get(this.post, "level");
                    })
                    .catch((err) => {
                        this.post.content = "‚ö†Ô∏è Êï∞ÊçÆÂä†ËΩΩÂºÇÂ∏∏";
                    });
            }
        },
        isEditMode() {
            // Ëé∑ÂèñÊúÄÊñ∞ÊîªÁï•
            this.getWikiNewestPost(this.isEditMode ? 0 : 1);
        },
    },
    components: {
        Article,
    },
};
</script>

